import axios from 'axios'

export default {
  getHomeList (params = {}, url = '/data/errorList') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    return axios.get(url, {params})
    // return Promise.resolve({
    //   data: {
    //     dev_one1: '10',
    //     dev_one2: '12',
    //     dev_one3: '13',
    //     dev_one5: '15',
    //     dev_one4: '10',
    //     dev_one6: '10'
    //   }
    // })
  },
  getInterfaceDate (param, url = '/data/interfaceTop') {
    axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
    axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
    return axios.get(url, {params: param})
  }
}



