<template>
    <div class="top" style="background:#F2F6FC;padding:10px">

        <el-row>
            <el-col :span="4">
                <div class="search">
                    <el-select v-model="service" placeholder="Service"  filterable >
                        <el-option v-for="s in serviceOption" :key="s" :value="s" :label="s"></el-option>
                    </el-select>

                </div>
            </el-col>
            <el-col :span="4">
                <div class="block">
                    <el-date-picker
                            v-model="date"
                            type="date"
                            placeholder="选择日期"
                            format="yyyy MM dd "
                            value-format="yyyyMMdd">
                    </el-date-picker>
                </div>
            </el-col>
            <el-col :span="4">
                <div class="but">
                    <el-button type="primary" @click="getInterfaceDate()">查询</el-button>
                </div>
            </el-col>

        </el-row>

        <el-row>
            <el-col :span="24">
                <div class="grid-content bg-purple-dark" style="background:#F2F6FC;padding:5px">
                    <el-card class="box-card" style="background:#FBFDFF;" >
                        <el-tabs v-model="currTab">
                            <el-tab-pane name="1" label="接口调用次数">
                                <el-table :data="homeList" @row-click="rowClick"  stripe="true" width="100%" align="right" :stripe="true"
                                          header-align="right" max-height="500">
                                    <el-table-column prop="key" label="接口名称"></el-table-column>
                                    <el-table-column prop="value" label="调用次数"></el-table-column>
                                </el-table>
                            </el-tab-pane>
                        </el-tabs>
                    </el-card>
                </div>
            </el-col>
            <el-col :span="24">
                <div style="background:#F2F6FC;padding:10px">
                    <el-dialog
                            :visible.sync="dialogVisible"
                            width="80%">
                        <i-echarts  style="width:100%;height: 700px;" :option="chartData"
                                    :loading="chartLoading"></i-echarts>
                    </el-dialog>

                </div>
            </el-col>
        </el-row>

    </div>
</template>

<script>
  import service from './service'
  import IEcharts from 'vue-echarts-v3/src/lite.js'
  import 'echarts/lib/chart/line'
  import 'echarts/lib/chart/pie'
  import 'echarts/lib/component/dataZoom'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/toolbox'
  import 'echarts/lib/component/title'
  export default {
    name: 'home',
    data () {
      return {
        dialogVisible: false,
        currTab: '1',
        service: '',
        interface: '',
        date: '',
        currRow: {},
        homeList: [],
        serviceList: [],
        serviceOption: [],
        chartLoading: false,
        chartData: {
          tooltip: {
            trigger: 'axis'
          },
          title: {
            text: 'Week Data'
          },
          toolbox: {
            feature: {
              dataZoom: {
                yAxisIndex: 'none'
              },
              restore: {},
              saveAsImage: {}
            }
          },
          dataZoom: [{
            type: 'slider',
            start: 0,
            end: 100
          }],
          xAxis: {
            type: 'category',
            data: []
          },
          yAxis: {
            type: 'value'
          },
          series: [{
            data: [],
            type: 'line',
            smooth: true
          }]
        }
      }
    },
    methods: {
      getInterfaceDate () {
        return service.getInterfaceDate({name: this.service, date: this.date}).then(({data = {}}) => {
          this.homeList = Object.keys(data).reduce((res, key, idx) => {
            res.push({key: key, value: data[key], index: idx + 1})
            return res
          }, [])
        })
      },
      rowClick (row) {
        this.interface = row.key
        this.loadChart()
        this.dialogVisible = true
      },
      loadChart() {
        this.chartLoading = true
        this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
        this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
        this.axios.get('/data/interfaceChart?face=' + this.interface, { params: { name: this.service}}).then((res) => {
          this.chartData.xAxis.data = res.data.yaxis
          this.chartData.title.text = res.data.name
          this.chartData.series = [{
            data: res.data.xaxis,
            type: 'line',
            smooth: true
          }]
        }).finally(() => {
          this.chartLoading = false
        })
      },
      baseData() {
        this.axios.defaults.headers.common['project'] = window.localStorage.getItem('pjValue')
        this.axios.defaults.headers.common['profile'] = window.localStorage.getItem('prValue')
        this.axios.get('/chart/getBaseData', { params: { type: 0 } })
          .then((res) => {
            this.serviceOption = res.data
          })
      }
    },
    computed: {},
    mounted () {
      // this.loadChart()
      this.baseData()
    },
    components: {
      IEcharts
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



